import React from 'react';
import { Link } from 'react-router-dom';
import { connectScreenSize } from 'react-screen-size';
import { mapScreenSizeToProps } from '../utils/helpers';

/* About Component */
export default connectScreenSize(mapScreenSizeToProps)(
class About extends React.Component {
  componentDidMount() {
    document.addEventListener('keydown', this.handleKeyDown);
  }
  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown, false);
  }
  handleKeyDown = ({ code }) => {
		if (code === 'Escape') this.props.history.push('/');
	}
  render() {
    // const quiz = meta.get('quiz');
    const { screen } = this.props;
    return (
      <div className='studyWrapper reviewContainer'>
				<div className='studyContainer'>
          <div className='quizHeader'>
            <div className='quizTitle'>
              <a
                target="_blank"
                rel="noopener noreferrer"
                className="fccLink"
                href="#">
                {/* <span>{quiz.get('titleForQuiz')}</span> */}
              </a>
            </div>
            {!screen.isMobile && <span id="return">
              <Link to='/'>
                <i className="fa fa-times-circle" aria-hidden="true"></i>
              </Link>
            </span>}
          </div>
          <div className='about'>
            <h2>தினப் பயிற்சிக் குழுவின் தமிழ்ப் பயிற்சி</h2>
            <h2>Tamil Practice by DailyPractice Team</h2>
            {/* <button className='aboutContribute'>
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="#">
                Contribute</a>
            </button><br /> */}
            <Link className='finishBtn' to='/'>
							<button>Return to Quiz Page</button>
						</Link>
          </div>
				</div>
			</div>
    )
  }
});
